//import React from 'react'
import axios from "axios";

const baseUrl = "/api/persons";
const getAll = async () => {
	const request = axios.get(baseUrl);
	try {
		const response = await request;
		return response.data;
	} catch (err) {
		return err.message;
	}
};
const create = async (newObject) => {
	const res = axios.post(baseUrl, newObject);
	try {
		const response = await res;
		console.log("response from service = ", response.data);
		return response.data;
	} catch (err) {
		console.log("error from service = ", err);
		return err.message;
	}
};
const remove = (id) => {
	const request = axios.delete(`${baseUrl}/${id}`);
	// return request
	return request.then((response) => response.data).catch((err) => err.message);
};
const update = (id, note) => {
	const request = axios.put(`${baseUrl}/${id}`, note);
	return request.then((response) => response.data).catch((err) => err.message);
};

const dataServices = { getAll, create, remove, update };
export default dataServices;
